.item{
  position: relative;
  outline-style: solid;
  outline-width: 1px;
  text-decoration: none;
  color:inherit;
  padding: .5em 1em;
  display: block;
}
.expired{
  opacity: .3;
}
.item:not(.expired):hover{
  outline-width: 3px;
}
.item__margin {
  display: block;
  float:left;
  width: 30%;
  padding: 1em;
}
.item__margin .item__row:nth-of-type(2) {
  border-bottom: 1px dotted gray;
}
.item__content {
  margin-top: 7em;
}
.item__row{
  margin: .5em 0;
}
.item__description{
  height: 3.6em;
  margin: 0;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  overflow: hidden;
}
.item__discount{
  position: absolute;
  border-radius: 50%;
  top: .5em;
  left: .5em;
  width: 2em;
  height: 2em;
  padding: 1em;
  color: white;
  text-align: center;
  font-size: 150%;
}
.item__oldprice{
  text-decoration: line-through;
}
.item__newprice{
  color: red;
  font-size: 200%;
  margin-right: .5em;
}
.item__from{
  float:right;
  margin-top: 1em;
}
.item__content .item__row:last-of-type{
  display: table;
  width: 100%;
}
.item__content .item__row:last-of-type span{
  display: table-cell;
  text-align: center;
}
.item__content .item__row:last-of-type span:not(:last-of-type) {
  border-right: 1px solid gray;
}
.item .item__image{
  display: block;
  width: 8em;
  float: right;
  height: 10.5em;
  margin-left: .2em;
}

/* HOT */
main .item__margin:first-of-type .item__row:nth-last-of-type(2) {
  border-top: 1px dotted gray;
}
.item__margin:first-of-type .item__image{
  float: none;
  width: 100%;
  height: 24em;
  margin: 0;
}
.item__margin:first-of-type .item__content{
  margin-top: 0;
}
.item__margin:first-of-type .item__description {
  margin: .5em 0;
}
.item__margin:first-of-type .item__newprice {
  font-weight: bold;
}
.item__image img{
  width: 100%;
}

@media screen and (max-width:962px) {
  .item__margin{
    width: 46%;
  }
}

@media screen and (max-width:760px) {
  .item__margin{
    width: auto;
    float:none;
  }
}



